import React ,{useState} from 'react';
import styles from './Pictureview.module.css';
import Imagezoom from './Imagezoom.js';
 const Pictureview = (props) => {
  const images = [
    {id:1, src:'/Picture/Image2.JPG', description:'Christmas 2020', styling: 'vertical'},
    {id:2, src:'/Picture/Image1.JPG', description:'Cake Smash', styling: 'vertical'},
    {id:3, src:'/Picture/Image7.JPG', description:'First Birthday 2019', styling: 'vertical'},
    {id:4, src:'/Picture/Image3.JPG', description:'Tulip Festival', styling: 'vertical'},
    {id:5, src:'/Picture/Image4.JPG', description:'Santorini 2017',styling: 'vertical'},
    {id:6, src:'/Picture/Image5.JPG', description:'Disney2017', styling:'Hori'},
    {id:7, src:'/Picture/Image6.JPG', description:'Venice2017', styling:'Hori'},
    {id:8, src:'/Picture/Image9.JPG', description:'Dallas 2014', styling:'vertical'},
    {id:9, src:'/Picture/Image8.JPG', description:'Where it all started', styling:'vertical'}
  ]
  
const[display,setDisplay] = useState(false);
const[imageurl,setUrl] = useState('');
const[imageOrient,setOrient] =useState('');
const[description,setdescript] = useState('');

const displayImage = (src,imageOrient,description) =>{
    setDisplay(true);
    setUrl(src);
    setOrient(imageOrient);
    setdescript(description);
    
}


const closeImage = () =>{
  setDisplay(false);
  setUrl('');
  setOrient('');
  setdescript('');
  console.log(imageurl,imageOrient);
}
  return (
    
    <div className = {styles.container}>
      
      <div className = {props.display === 'grid' ? styles.gridView : styles.listView}>
       { images.map((image) => (
         
          <div className = {styles.card}  >
          <img src = {process.env.PUBLIC_URL + image.src} 
               alt = {image.description} 
               styling = {image.styling}
               className = {image.styling === 'vertical' ? styles.image : styles.imageHori}
               key = {image.id} 
               onClick = {(e) => {displayImage(e.target.src,e.target.attributes[2].nodeValue,e.target.alt)}}/>

         <span className ={image.styling === 'vertical' ? styles.imgCaption : styles.imgCaption2 }>{image.description}</span> 

         {display && <Imagezoom imageStyling= {imageOrient} closeImage= {closeImage} imageurl = {imageurl} imgdescription={description}/>}
            </div>
            
          
       ))}
      </div>
   </div> 
  )
}
      export default Pictureview;

      