import classes from './Gallery.module.css';
import React, {useState} from 'react';
import Pictureview from './Pictureview';

const Gallery = () => {

  const [list,setList] = useState(false);
  const[grid,setGrid] = useState(true);

  
  const displayGrid = () =>{
    
    setGrid(true);
    setList(false);
  }
  const displayList = () =>{
    setList(true);
    setGrid(false);
  }

  
  return (
    <div className = {classes.container1}>
     <i className={`${classes.listViewicon} fas fa-list fa-lg` } title = "List View" onClick ={displayList}>
     <span className = {classes.listText}>Listview</span>
     </i>
      {' '}
      <i  className = {`${classes.gridViewicon} fas fa-th fa-lg`} title = "Grid View"onClick = {displayGrid}></i> 
      
      <br></br>

      <h3 className = {classes.heading}>PICTURE GALLERY </h3>
      <div className ={classes.container2}>
     
        {list && <Pictureview  />}
        {grid && <Pictureview display ='grid' />}
      </div>
      </div>
  )
}

export default  Gallery;