import React from 'react';

import styles from './Intro.module.css';

const Intro = () => {

  
  const hobbies = ['Cooking','DIY projects','Painting'];
  const strength = ['Honesty','Flexibility','Creativity'];
  const weakness = ['Stress out easily', 'Self-critical','Poor time management'];
  const skillset = ['HTML5','CSS3','Javascript','React'];


  /* const list = [
    ['Cooking','DIY projects','Painting'],
    ['Honesty','Flexibility','Creativity'],
    ['Stress out easily', 'Self-critical','Poor time management'],
    ['HTML5','CSS3','Javascript','React']
  ] */

  return (
    <div className = {styles.container}>
      <div className ={styles.container2}>
      <p>
        I am Ramya,mother of a two year old, an aspiring programmer and a hardcore Indian. 
        I would like to believe that I am well versed in the basics of web development and very much motivated to learn some server side languages and would like to become a full-stack developer eventually. 
        I believe in hardwork and consistency and my motto in life is ‘Everything you want is waiting on the other side of Consistency’.
        </p>
        <p>
        Apart from learning I am a huge environment enthusiast. I am completely against using single use plastics and try my best to keep my environment plastic free. I believe planting trees are the best way to combat global warming  and I strive to lead a more sustainable lifestyle.&nbsp;&nbsp;&nbsp;&nbsp;
        </p>
        <p>I give a lot of importance to fitness. Currently, very much into weight training and a big believer of 'Healthy body leads to a healthy mind'

        I strongly believe that men and women are equal and would love to work in an environment where there is no gender bias.
        <p>
        I aspire to be a better human being everyday. 
        </p>
        </p>
      </div>

     
      
      <hr style={{backgroundColor:'black',color:'black'}}></hr>
    <div className ={styles.flexContainer}>
     {/*  box1  */}
      <div className ={styles.box1}>
      <h3 className = {styles.heading1}>Hobbies</h3>
      <hr></hr>
       <ul>
          {hobbies.map((h) => (
          <li>{h}</li>
          ))}
       </ul>
       </div>
      
      {/* box 2 */}

      <div className ={styles.box1}>
      <h3 className = {styles.heading1}>Strength</h3>
      <hr></hr>
       <ul>
          {strength.map((h) => (
          <li>{h}</li>
          ))}
       </ul>
       </div>
      
      {/* box 3 */}

      <div className ={styles.box1}>
      <h3 className = {styles.heading1}>Weakness</h3>
      <hr></hr>
       <ul>
          {weakness.map((h) => (
          <li>{h}</li>
          ))}
       </ul>
       </div>

        {/* box 4 */}

      <div className ={styles.box1}>
      <h3 className = {styles.heading1}>Skillset</h3>
      <hr></hr>
       <ul>
          {skillset.map((h) => (
          <li>{h}</li>
          ))}
       </ul>
       </div>
      </div>
    </div>
  )
}

export default Intro;
