import React from 'react';
import styles from './Imagezoom.module.css';
const Imagezoom = (props) => {
  return (
    <div className = {styles.modal}>
      <div className = {props.imageStyling == 'vertical' ? styles.modalContent : styles.modalContent2}>
        <div className = {styles.header}>
          <span className = {props.imageStyling == 'vertical' ? styles.caption: styles.caption2}>{props.imgdescription} </span>
          <button className = {styles.closebtn} onClick = {props.closeImage}>
            Close
          </button>
        </div>
      <img src = {props.imageurl} alt = "No picture" className = {props.imageStyling == 'vertical' ? styles.imagezoom : styles.imagezoomHori}></img>
      </div>
    </div>
  )
}
export default Imagezoom;